.app-header__subtitle {
  font-size: 1.2rem;
  color: dimgray;
}

.btn-active {
  background-color: brown;
}

input {
  height: 47px;
  border: solid 2px gray;
  border-radius: 5px;
}

.important {
  color: rgb(30, 104, 30);
  font-weight: bold;
}
.span-style {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  height: 50px;
  border: 2px solid rgb(233, 223, 223);
  border-radius: 5px;
  padding-left: 5px;
}

button {
  margin-right: 5px;
}

.done {
  text-decoration: line-through;
}

.list-group {
  margin-top: 10px;
  list-style-type: none;
}

.btn-add {
  margin-top: 20px;
}

.main-app-wrap {
  margin: 0 auto;
  margin-top: 5%;
  width: 50%;
}

.flex-row-wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
}

